

.content {
  display: flex;
  position: relative;
  margin-top: -75px;
  overflow: hidden;
  .img {
    position: relative;
    overflow: hidden;
    img {
      width: 100%;
      height: auto; } }
  .img-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #000;
    z-index: 10;
    opacity: 1; }
  .text {
    position: absolute;
    bottom: 80px;
    left: 0;
    width: 100%;
    color: #fff;
    text-align: center;
    h1 {
      font-size: 82px;
      text-transform: uppercase;
      font-family: Butler;
      font-weight: 300;
      margin-bottom: 15px; }
    h5 {
      font-size: 28px;
      letter-spacing: 20px;
      text-transform: uppercase;
      font-weight: 100; } } }


.common {
  width: 50%;
  height: 100vh;
  position: relative;
  &:hover {
    .btn a {
      opacity: 1; } } }


.btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
  a {
    display: inline-block;
    border: 1px solid #fff;
    padding: 25px 60px;
    color: #fff;
    text-transform: uppercase;
    text-decoration: none;
    opacity: 0;
    transition: all ease 500ms;
    &:hover {
      background-color: #fff;
      color: #000; } } }


